import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AddWorkComponent } from './adminPanel/Components/add-work/add-work.component';
import { ContactMessagesComponent } from './adminPanel/Components/contact-messages/contact-messages.component';
import { DashboardComponent } from './adminPanel/Components/dashboard/dashboard.component';
import { SettingsComponent } from './adminPanel/Components/settings/settings.component';
import { DefaultComponent } from './adminPanel/default/default.component';
import { AboutComponent } from './mainPortal/about/about.component';
import { AwkwardSpacesComponent } from './mainPortal/awkward-spaces/awkward-spaces.component';
import { BedroomsComponent } from './mainPortal/bedrooms/bedrooms.component';
import { ContactComponent } from './mainPortal/contact/contact.component';
import { HomeOfficeComponent } from './mainPortal/home-office/home-office.component';
import { HomeComponent } from './mainPortal/home/home.component';
import { KitchenComponent } from './mainPortal/kitchen/kitchen.component';
import { LivingSpacesComponent } from './mainPortal/living-spaces/living-spaces.component';
import { LoginComponent } from './mainPortal/login/login.component';
import { MyWorksComponent } from './mainPortal/my-works/my-works.component';
import { RegisterComponent } from './mainPortal/register/register.component';
import { AuthGuard } from './shared/guards/auth.guard';

const routes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'bedrooms', component: BedroomsComponent},
  {path: 'home-office', component: HomeOfficeComponent},
  {path: 'living-spaces', component: LivingSpacesComponent},
  {path: 'awkward-spaces', component: AwkwardSpacesComponent},
  {path: 'kitchen', component: KitchenComponent},
  {path: 'about', component: AboutComponent},
  {path: 'login', component: LoginComponent},
  {path: 'contact', component: ContactComponent},
  {path: 'myWorks', component: MyWorksComponent},
  {path: 'admin', canActivate: [AuthGuard], component: DefaultComponent,
  children: [
    {path: '', component: DashboardComponent},
    {path: 'messages', component: ContactMessagesComponent},
    {path: 'addWork', component: AddWorkComponent},
    {path: 'settings', component: SettingsComponent}
  ]
},
{path: 'register', component: RegisterComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
