import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { MainService } from 'src/app/shared/services/main.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
@ViewChild('FileSelect') FileSelect: ElementRef;
MyForm: FormGroup;
imageData : any;
message: any = "";

  constructor(private _fb: FormBuilder, private service: MainService, private flashMessage: FlashMessagesService) { this.formGroupModel() }

  ngOnInit(): void {
  }

  formGroupModel(){
    this.MyForm = this._fb.group({
      name:['', [Validators.required, Validators.minLength(3), Validators.maxLength(20), Validators.pattern(/^[a-zA-Z ]*$/)]],
      username:['', [Validators.required, Validators.minLength(3), Validators.maxLength(20), Validators.pattern(/^[a-zA-Z ]*$/)]],
      email:['', [Validators.required, Validators.email]],
      password:['', [Validators.required, Validators.minLength(6), Validators.maxLength(20)]],
      mobile: ['', [Validators.required, Validators.minLength(11), Validators.maxLength(20), Validators.pattern(/^[0-9*#+]*$/)]],
      address: ['', [Validators.required, Validators.minLength(5)]],
      file: ['']
    });
  }
  GetFiles(event) {
    this.imageData = event.target.files[0];
    this.MyForm.get('file').setValue(this.imageData);
    }

    onSubmit(){
      const formData = new FormData();
      formData.append('name', this.MyForm.get('name').value);
      formData.append('username', this.MyForm.get('username').value);
      formData.append('email', this.MyForm.get('email').value);
      formData.append('password', this.MyForm.get('password').value);
      formData.append('mobile', this.MyForm.get('mobile').value);
      formData.append('address', this.MyForm.get('address').value);
      formData.append('file', this.MyForm.get('file').value);
        this.service.registerAdmin(formData).subscribe((res:any) => {
            this.message = res.message,
            this.flashMessage.show(this.message, {cssClass:'success', timeout:5000})
          })
          this.FileSelect.nativeElement.value= null;
          this.MyForm.reset();
    }
}
