import { Component, OnInit } from '@angular/core';
import { MainService } from 'src/app/shared/services/main.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  _RecentWorkImageDirectory = `/RecentWorkImages/`;
  _HeaderImageDirectory = '/HeaderImages/'
  _ImageOneUrl:any;
  _ImageTwoUrl:any;
  _ImageThreeUrl:any;
  _HeaderImageUrl:any;
  _HeaderHeading:any;
  _HeaderDescription:any;
  BackGroundHeader={
    'background-image':'',
    'background-repeat':'no-repeat'
  }
  constructor(private _MainService:MainService) { }

  ngOnInit(): void {
    this._MainService.GetRecentWorkImages().subscribe((docs:any)=>{
      docs.Result.map(ImageData=>{
        if(ImageData.ImageCategory === 'Image One'){
          this._ImageOneUrl = this._RecentWorkImageDirectory + ImageData.imageUrl;
        }else if(ImageData.ImageCategory === 'Image Two'){
          this._ImageTwoUrl = this._RecentWorkImageDirectory + ImageData.imageUrl;
        }else if(ImageData.ImageCategory === 'Image Three'){
          this._ImageThreeUrl = this._RecentWorkImageDirectory + ImageData.imageUrl;
        }
      })
    })
    
    this._MainService.GetHeaderImages().subscribe((DataComingFromBackEnd:any)=>{
      DataComingFromBackEnd.Result.map(HeaderData=>{
        if(HeaderData.ImageCategory === 'Home'){
          this._HeaderHeading = HeaderData.Headings;
          this._HeaderDescription = HeaderData.Description;
          this._HeaderImageUrl = this._HeaderImageDirectory + HeaderData.imageUrl
          this.BackGroundHeader['background-image'] = 'url('+this._HeaderImageUrl+')';
        }else {
          return null;
        }
      })
    })
  }

}
