import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class MainService {

  adminLoginUrl = "/admin/loginAdmin";
  adminRegisterUrl = "/admin/addAdmin";
  postContactMessageUrl = "/other/addMessage";
  getContactMessageUrl = "/other/getMessage";
  addWorkUrl = "/work/addWork";
  PostRecentWorkImageUrl = "/work/addRecentWork";
  GetRecentWorkImageUrl = "/work/addRecentWork";
  PostHeaderImagesUrl = "/work/addHeaderImage";
  GetHeaderImagesUrl = "/work/addHeaderImage";
  PostMultipleImagesForYourRecentWorkUrl = "/work/addRecentWorkMulitpleImages";
  GetMultipleImagesForYourRecentWorkUrl = "/work/addRecentWorkMulitpleImages";
  DeletSingleImageFromRecentWorkGalleryUrl = "/work/deleteRecentWorkSingleImage";
  SendEmailUrl = "/other/SendEmail";

  constructor(private http: HttpClient, private router: Router) { }

  GetMultipleImagesForYourRecentWork() {
    return this.http.get(this.GetMultipleImagesForYourRecentWorkUrl);
  }

  PostMultipleImagesForYourRecentWork(data) {
    return this.http.post(this.PostMultipleImagesForYourRecentWorkUrl, data);
  }

  PostHeaderImages(data) {
    return this.http.post(this.PostHeaderImagesUrl, data);
  }

  GetHeaderImages() {
    return this.http.get(this.GetHeaderImagesUrl);
  }

  PostRecentWorkImages(data) {
    return this.http.post(this.PostRecentWorkImageUrl, data);
  }

  GetRecentWorkImages() {
    return this.http.get(this.GetRecentWorkImageUrl);
  }

  registerAdmin(data) {
    return this.http.post(this.adminRegisterUrl, data);
  }
  loginAdmin(data) {
    console.log(data);
    return this.http.post(this.adminLoginUrl, data);
  }
  getLoggedInEmployeeDataFromBackEnd(data) {
    localStorage.setItem('user_token', data.data.token);
    localStorage.setItem('username', data.data.data.name);
    this.router.navigate(['/admin']);
  }

  GetUserNameFromLocalStorage(){
    let _UserName = localStorage.getItem('username');
    return _UserName;
  }
  
  IsLoggedIn() {
    return localStorage.getItem('user_token') != null;
  }
  contactMessage(data) {
    return this.http.post(this.postContactMessageUrl, data);
  }
  getMessages() {
    return this.http.get(this.getContactMessageUrl);
  }
  addWork(data) {
    return this.http.post(this.addWorkUrl, data);
  }

  DeleteSingleImageFromRecentWorkGallery(Data) {
    return this.http.post(`${this.DeletSingleImageFromRecentWorkGalleryUrl}`,Data);
  }

  SendEmail(Data){
    return this.http.post(`${this.SendEmailUrl}`,Data);
  }
}