import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { MainService } from 'src/app/shared/services/main.service';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {

  AddRecentWorkForm: FormGroup;
  HeaderImageForm: FormGroup;
  @ViewChild('FileSelect') FileSelect: ElementRef;
  SelectedImagesLength: any;
  DissabledButton: boolean;
  imageData: any;
  constructor(private _FormBuilder: FormBuilder, private _FlashMessages: FlashMessagesService, private _MainService: MainService) { this.CreateRecentWorkForm(), this.CreatHeadersForm() }

  ngOnInit(): void {
  }


  //**********************Header Images Logic**********************//
  CreatHeadersForm(){
    this.HeaderImageForm = this._FormBuilder.group({
      Headings: ['', Validators.required],
      Description: ['', Validators.required],
      ImageCategory: ['', Validators.required],
      ImageUrl: ['']
    })
  }
  GetHeaderImage(event){
    this.SelectedImagesLength = event.target.files.length;
    if (event.target.files.length < 1) {
      this.DissabledButton = true;
      this.FileSelect.nativeElement.value = null;
      return null;
    } else {
      this.imageData = event.target.files[0];
      this.HeaderImageForm.get('ImageUrl').setValue(this.imageData);
      this.DissabledButton = false;
    }
  }


   //Posting the Data to the Backend by Submiting form
   SaveHeaderImageFormData() {
    let _FormData = new FormData();
    _FormData.append('Headings', this.HeaderImageForm.get('Headings').value);
    _FormData.append('Description', this.HeaderImageForm.get('Description').value);
    _FormData.append('ImageCategory', this.HeaderImageForm.get('ImageCategory').value);
    _FormData.append('HeaderImage', this.HeaderImageForm.get('ImageUrl').value);

    this._MainService.PostHeaderImages(_FormData).subscribe((DataComingFromBackend:any) => {
      let _Message = DataComingFromBackend.Message;
          this._FlashMessages.show(_Message, { cssClass: "alert-success", timeout: 8000 });

    })

    this.FileSelect.nativeElement.value = null;
    this.HeaderImageForm.reset();
  }
  //Posting the Data to the Backend by Submiting form

 //**********************Header Images Logic**********************//







  //**********************RecentWork Logic**********************//

  CreateRecentWorkForm() {
    this.AddRecentWorkForm = this._FormBuilder.group({
      ImageCategory: ['', Validators.required],
      ImageUrl: ['']
    })
  }

  //Get Image with its Url
  GetFiles(event) {
    this.SelectedImagesLength = event.target.files.length;
    if (event.target.files.length < 1) {
      this.DissabledButton = true;
      this.FileSelect.nativeElement.value = null;
      return null;
    } else {
      this.imageData = event.target.files[0];
      this.AddRecentWorkForm.get('ImageUrl').setValue(this.imageData);
      this.DissabledButton = false;
    }
  }
  //Get Image with its Url

  //Posting the Data to the Backend by Submiting form
  SaveRecentWorkFormData() {
    let _FormData = new FormData();
    _FormData.append('ImageCategory', this.AddRecentWorkForm.get('ImageCategory').value);
    _FormData.append('ImageUrl', this.AddRecentWorkForm.get('ImageUrl').value);

    this._MainService.PostRecentWorkImages(_FormData).subscribe((DataComingFromBackend:any)=> {
      let _Message = DataComingFromBackend.Message;
          this._FlashMessages.show(_Message, { cssClass: "alert-success", timeout: 8000 });
    })

    this.FileSelect.nativeElement.value = null;
    this.AddRecentWorkForm.reset();
  }
  //Posting the Data to the Backend by Submiting form

  //**********************RecentWork Logic**********************//
}
