import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { MainService } from 'src/app/shared/services/main.service';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {
  MyForm: FormGroup;
  message: any;
  _HeaderImageDirectory = '/HeaderImages/';
  _HeaderImageUrl:any;
  _HeaderHeading:any;
  _HeaderDescription:any;
  BackGroundHeader={
    'background-image':'',
    'background-repeat':'no-repeat'
  }
  constructor(private service: MainService, private flashMessage: FlashMessagesService, private _fb: FormBuilder,private _MainService:MainService) { this.formGroupModel() }


  ngOnInit(): void {
    this._MainService.GetHeaderImages().subscribe((DataComingFromBackEnd:any)=>{
      DataComingFromBackEnd.Result.map(HeaderData=>{
        if(HeaderData.ImageCategory === 'Contact'){
          this._HeaderHeading = HeaderData.Headings;
          this._HeaderDescription = HeaderData.Description;
          this._HeaderImageUrl = this._HeaderImageDirectory + HeaderData.imageUrl;
          this.BackGroundHeader['background-image'] = 'url('+this._HeaderImageUrl+')';
        }else {
          return null;
        }
      })
    })
  }
formGroupModel(){
    this.MyForm = this._fb.group({
      firstname:['', [Validators.required, Validators.minLength(3), Validators.maxLength(20), Validators.pattern(/^[a-zA-Z ]*$/)]],
      subject:['', [Validators.required, Validators.minLength(3), Validators.maxLength(20), Validators.pattern(/^[a-zA-Z ]*$/)]],
      email:['', [Validators.required, Validators.email]],
      mobile: ['', [Validators.required]],
      address: ['', [Validators.required, Validators.minLength(5)]],
      message: ['', [Validators.required, Validators.minLength(5)]]
    });
  }
  onSubmit(){
      this.service.SendEmail(this.MyForm.value).subscribe((res:any) => {
          this.message = res.Message,
          this.flashMessage.show(this.message, {cssClass:'alert', timeout:5000})
        })
        this.MyForm.reset();
  }
}
