import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { MainService } from 'src/app/shared/services/main.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  MyForm: FormGroup;
  message: any;
  constructor(private flashMessage: FlashMessagesService, private _fb: FormBuilder,private _MainService:MainService) { this.formGroupModel() }

  ngOnInit(): void {
  }

  formGroupModel(){
    this.MyForm = this._fb.group({
      firstname:['', [Validators.required, Validators.minLength(3), Validators.maxLength(20), Validators.pattern(/^[a-zA-Z ]*$/)]],
      subject:['', [Validators.required, Validators.minLength(3), Validators.maxLength(20), Validators.pattern(/^[a-zA-Z ]*$/)]],
      email:['', [Validators.required, Validators.email]],
      mobile: ['', [Validators.required]],
      address: ['', [Validators.required, Validators.minLength(5)]],
      message: ['', [Validators.required, Validators.minLength(5)]]
    });
  }
  onSubmit(){
      this._MainService.SendEmail(this.MyForm.value).subscribe((res:any) => {
          this.message = res.Message,
          this.flashMessage.show(this.message, {cssClass:'alert', timeout:5000})
        })
        this.MyForm.reset();
  }
}
