import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { MainService } from 'src/app/shared/services/main.service';

@Component({
  selector: 'app-add-work',
  templateUrl: './add-work.component.html',
  styleUrls: ['./add-work.component.css']
})
export class AddWorkComponent implements OnInit {
  _MultipleImagesForRecentWorkDirectory = `/RecentWorkMultipleImages/`;
  _HomeData: any = [];
  _BedroomsData: any = [];
  _HomeOfficeData: any = [];
  _LivingSpacesData: any = [];
  _AwkwardSpacesData: any = [];
  _KitchenData: any = [];


  FromForYourWork: FormGroup;
  @ViewChild('FileSelect') FileSelect: ElementRef;
  SelectedImagesLength: any;
  DissabledButton: boolean;
  imageData: any;
  ImageDataArray: any = [];
  constructor(private _FormBuilder: FormBuilder, private _FlashMessages: FlashMessagesService, private _MainService: MainService) { this.CreatYourWorkForm() }

  ngOnInit(): void {
    this._MainService.GetMultipleImagesForYourRecentWork().subscribe((DocumentComingFromBackend: any) => {
      DocumentComingFromBackend.Result.map(Data => {
        if (Data.ImageCategory === 'Home') {
          Data.imgDetails.map(ImageData => {
            let _RequestedObject = {
              ImageCategory:Data.ImageCategory,
              imageName:ImageData.imageName,
              imageMimeType:ImageData.imageMimeType,
              imageUrl:ImageData.imageUrl
            }
            this._HomeData.push(_RequestedObject);
          })
        } else if (Data.ImageCategory === 'Bedrooms') {
          Data.imgDetails.map(ImageData => {
            let _RequestedObject = {
              ImageCategory:Data.ImageCategory,
              imageName:ImageData.imageName,
              imageMimeType:ImageData.imageMimeType,
              imageUrl:ImageData.imageUrl
            }
            this._BedroomsData.push(_RequestedObject);
          })
        } else if (Data.ImageCategory === 'Home Office') {
          Data.imgDetails.map(ImageData => {
            let _RequestedObject = {
              ImageCategory:Data.ImageCategory,
              imageName:ImageData.imageName,
              imageMimeType:ImageData.imageMimeType,
              imageUrl:ImageData.imageUrl
            }
            this._HomeOfficeData.push(_RequestedObject);
          })
        } else if (Data.ImageCategory === 'Living Spaces') {
          Data.imgDetails.map(ImageData => {
            let _RequestedObject = {
              ImageCategory:Data.ImageCategory,
              imageName:ImageData.imageName,
              imageMimeType:ImageData.imageMimeType,
              imageUrl:ImageData.imageUrl
            }
            this._LivingSpacesData.push(_RequestedObject);
          })
        } else if (Data.ImageCategory === 'Awkward Spaces') {
          Data.imgDetails.map(ImageData => {
            let _RequestedObject = {
              ImageCategory:Data.ImageCategory,
              imageName:ImageData.imageName,
              imageMimeType:ImageData.imageMimeType,
              imageUrl:ImageData.imageUrl
            }
            this._AwkwardSpacesData.push(_RequestedObject);
          })
        } else if (Data.ImageCategory === 'Kitchen') {
          Data.imgDetails.map(ImageData => {
            let _RequestedObject = {
              ImageCategory:Data.ImageCategory,
              imageName:ImageData.imageName,
              imageMimeType:ImageData.imageMimeType,
              imageUrl:ImageData.imageUrl
            }
            this._KitchenData.push(_RequestedObject);
          })
        } else {
          return null;
        }
      })
    })
  }

  //**********************Header Images Logic**********************//
  CreatYourWorkForm() {
    this.FromForYourWork = this._FormBuilder.group({
      ImageCategory: ['', Validators.required],
    })
  }
  GetMultipleImagesForFromForYourWork(event) {
    this.SelectedImagesLength = event.target.files.length;
    if (event.target.files.length > 5) {
      this.DissabledButton = true;
      this.FileSelect.nativeElement.value = null;
      this.FromForYourWork.reset();
      this.ImageDataArray = [];
      return null;
    } else {
      for (let i = 0; i < event.target.files.length; i++) {
        this.ImageDataArray.push(event.target.files[i]);
      }
      this.DissabledButton = false;
    }
  }
  AddYourWork() {
    const _FormData = new FormData();
    _FormData.append('ImageCategory', this.FromForYourWork.get('ImageCategory').value);
    this.ImageDataArray.map(ImageData => {
      _FormData.append('MultipleImages', ImageData);
    })

    this._MainService.PostMultipleImagesForYourRecentWork(_FormData).subscribe((DocumentsComingFromBackend: any) => {

      let _Message = DocumentsComingFromBackend.Message;
      this._FlashMessages.show(_Message, { cssClass: "alert-success", timeout: 8000 });
      this.ngOnInit();
    })
    this.FileSelect.nativeElement.value = null;
    this.FromForYourWork.reset();
    this.ImageDataArray = [];
    this._HomeData = [];
    this._BedroomsData = [];
    this._AwkwardSpacesData = [];
    this._KitchenData = [];
    this._LivingSpacesData = [];
    this._HomeOfficeData = [];
  }

  DeleteImage(Data) {
    let _ImageDetails = Data;
    this._MainService.DeleteSingleImageFromRecentWorkGallery(_ImageDetails).subscribe((DocumentsComingFromBackEnd:any)=>{
    this._HomeData = [];
    this._BedroomsData = [];
    this._AwkwardSpacesData = [];
    this._KitchenData = [];
    this._LivingSpacesData = [];
    this._HomeOfficeData = [];
      this.ngOnInit();
    })
  }
}
