import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validator, Validators } from '@angular/forms';
import { MainService } from 'src/app/shared/services/main.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  MyForm: FormGroup;
  message: any = "";
  constructor(private formBuilder: FormBuilder, private service: MainService, private flashMessage: FlashMessagesService) { this.MyFormModel() }

  ngOnInit(): void {
  }
  MyFormModel() {
    this.MyForm = this.formBuilder.group({
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(20)]]
    });
  }
  onSubmit() {
    console.log(this.MyForm.value);
    this.service.loginAdmin(this.MyForm.value).subscribe((res: any)=>{
      if (!res.data) {
        this.message = res.message,
          this.flashMessage.show(this.message, { cssClass: "alert text-center", timeout: 4000 });
      } else if(res.data.token) {
        this.service.getLoggedInEmployeeDataFromBackEnd(res);
      }
    });
  }
}
